html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: 'MuseoModerno', cursive, sans-serif;
  margin: 0; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

button {
  padding: 0; }

a {
  text-decoration: none; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

body {
  background: linear-gradient(to bottom, #582781, #A468D5); }

.wrapper {
  max-width: 1000px;
  height: 100vh;
  padding: 100px 0;
  margin: 0 auto; }

.title {
  font-size: 40px;
  line-height: 40px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 30px;
  color: #ffffff; }

.instruction {
  color: #ffffff; }

.instruction__description {
  display: block;
  margin-top: 10px;
  text-align: center; }

.sort {
  position: relative;
  padding: 100px 0 50px; }

.sort__list {
  display: flex;
  justify-content: space-between;
  max-width: 730px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #A468D5;
  border-radius: 15px; }

.sort__item {
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 20px;
  line-height: 50px;
  border: 2px solid #ffffff;
  border-radius: 15px;
  transition: 0.3s;
  background-color: #ffffff;
  color: #582781; }
  .sort__item--prev {
    border-color: red; }
  .sort__item--next {
    border-color: green; }

.sort__target {
  position: absolute;
  top: 50px;
  transition: 0.3s;
  color: #ffffff; }
  .sort__target::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -40px;
    transform: translateX(-2px);
    width: 4px;
    height: 40px;
    background-color: #ffffff; }

.controls {
  text-align: center; }

.controls__btn {
  width: 250px;
  margin: 0 auto;
  padding: 10px 0;
  transition: 0.3s;
  cursor: pointer;
  outline: none;
  border: 2px solid #ffffff;
  border-radius: 20px;
  background-color: transparent;
  font-size: 20px;
  color: #ffffff; }
  .controls__btn--next {
    margin-right: 15px; }
  .controls__btn:hover {
    box-shadow: 0 0 3px 0 #ffffff;
    background-color: #ffffff;
    color: #582781; }

.info {
  margin-bottom: 50px;
  text-align: center;
  font-size: 24px;
  color: #ffffff; }
  .info span {
    color: red; }
